<template>
  <div class="appointment-details">
    <venue-head title="场馆详情" down="" ></venue-head>
    <ul class="appointmetLists">
      <li v-for="(item, index) in detailist" :key="index">
        <span class="name">{{item.name}}</span>：
        <template v-if='item.device'>
          <span v-for="(list, key) in detaiList[item.key]" :key='key'>{{list[item.device]}}</span>
        </template>
        <span v-else>{{detaiList[item.key]}}</span>
        <template v-if='item.key=="number"'>
          <span>人</span>
        </template>
      </li>
    </ul>
    <div class="imge-title" v-if="imgs.length">图片</div>
    <ul class="imgLists" v-if="imgs.length">
      <li v-for="(item, index) in imgs" :key="index">
        <img src="/static/images/scheduleIco.png" alt="">
      </li>
    </ul>
    <div class="venue-btn">
      <router-link :to="{name: 'submitReservation', params: {paths: this.$route.params.paths, item: this.detaiList}}">
        <div class="btn">预约</div>
      </router-link>
    </div>
  </div>
</template>
<script>
import VenueHead from './Head.vue'
export default {
  data () {
    return {
      detailist: [
        {name: '名 称', key: 'roomName'},
        {name: '位 置', key: 'address'},
        {name: '容纳量', key: 'number'},
        {name: '设 备', key: 'equips', device: 'stem'}
      ],
      detaiList: {},
      imgs: []
    }
  },
  created () {
    const item = this.$route.params.item
    this.detaiList = item
    if(item.detailImg) {
      this.imgs = item.detailImg.split(',')
    }
  },
  
  components: {
    VenueHead
  }
}
</script>
<style lang="less" scoped>
.appointment-details {
  background: #f5f7fa;
  height: 100%;
  .appointmetLists {
    background: #fff;
    padding: 1.25rem /* 20/16 */;
    color: #666;
    li:not(:first-child) {
      padding-top: 14px;
    }
    .name {
      display: inline-block;
      width: 2.625rem /* 42/16 */;
    }
  }
  .imge-title {
    padding: .625rem 1.25rem;
  }
  .imgLists {
    text-align: center;
    background: #fff;
    padding: .625rem 0;
    li {
      display: inline-block;
      width: 30%;
      background: #9898;
      margin: 4px;
      img {
        display: block;
        margin: 0 auto;
        width: 100%;
        height: auto;
      }
    }
  }
  .venue-btn {
    background: #fff;
    position: fixed;
    left: 0;
    right: 0;
    bottom: .125rem /* 2/16 */;
    .btn {
      width: 80%;
      margin: .25rem auto/* 4/16 */;      
      text-align: center;
      background: #1f95ff;
      border-radius: .3125rem /* 5/16 */;
      padding: .625rem /* 10/16 */ 0;
      color: #fff;
    }
  }
}
</style>



